},\n    created() {\n        this.$root.$on(\"updateinvi\", this.setinvi);\n    },\n    mounted() {\n        document.onreadystatechange = () => {\n            if (document.readyState == \"complete\") {\n                // Only call setSplider if we're on a page with the nav-slider element\n                if (document.getElementById('nav-slider')) {\n                    this.setSplider();\n                    if(this.$root?.$children[2]?.$children[1]?.spliderEvent){\n                        this.$root?.$children[2]?.$children[1]?.spliderEvent();\n                    }\n                }\n            }\n        };\n        window.addEventListener(\"resize\", this.setWidth);\n        this.$nextTick(() => {\n            if (this.$refs[this.link]) {\n                this.$refs[this.link].scrollIntoView({\n                    behavior: \"smooth\",\n                    block: \"start\",\n                    inline: \"nearest\",\n                });\n            }\n        });\n    },\n};\n</script>\n\n<style scoped>\n.guest-invi-navbar-cont {\n    position: fixed;\n    display: flex;\n    justify-content: center;\n    top: 91px;\n    z-index: 9999999999;\n    width: 100%;\n}\n.guest-invi-navbar {\n    background: #461952;\n    border-radius: 27px;\n    padding: 8px 13px !important;\n}\n.bottom-nav {\n    display: flex;\n    justify-content: space-between;\n    overflow-x: auto;\n    -ms-overflow-style: none; \n    scrollbar-width: none; \n}\n.bottom-nav::-webkit-scrollbar {\n    display: none;\n}\n.guest-invi-navbar  a {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 15px;\n    line-height: 22px;\n    text-align: center;\n    color: #9a9a9a;\n    padding: 8px 80px;\n    cursor: pointer;\n    white-space: nowrap;\n    text-decoration: none;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n.guest-invi-navbar  a > span {\n    width: 46px;\n    height: 46px;\n    margi